@skin-brand-primary:         @brand-primary;   
@wrapper-width:                1336px;

.skin-primay{
    .content-router li{
        & + li::before{
            color: #ccc;
        }
        &,a{
            color: @skin-brand-primary;
        }
        &.active{
            color: #999;
            cursor: default;
        }
    }
    .nav{
        .open > a{
            border-color: #ccc;
        }
    }
    .nav-tabs > li > a:hover {
        background-color: #fff;
        border-color: transparent;
        border-bottom-color: #ccc;
    }
    .sidebar-menu{
        &>li.header{
            color: @skin-brand-primary;
            background: #1a2226;
        }
        &>li>a{
            color: #4d97b9;
            &:hover{
                text-decoration: none;
                color: #fff;
                background: #1e282c;
            }
        }
        &>li.treeview>ul.treeview-menu li{
            a{
                color: #8aa4af;
            }
            &.active > a,
            & > a:hover{
                color: #fff;
                text-decoration: none;
            }
        } 
        &>li.active > a{
            color: #fff;
            border-left-color: #3c8dbc;
        }
        &>li.menu-open > ul.treeview-menu{
            padding: 0 2px;
            background: #2c3b41;
        }
        
    }
    .form-control:focus {
        border-color: @skin-brand-primary;
        -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(127,187,56, .6);
        box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(127,187,56, .6);
    }
    .form-search{
        .input-group-btn .btn{
            background: #fff;
            border-color: #ccc;
            color: @skin-brand-primary;
            &:focus{
                border-color: @skin-brand-primary;
            }
            &:hover{
                border-color: @skin-brand-primary;
            }
        }
    }
    .lime-pagination{
        li{
            .link:hover{
                color: @skin-brand-primary;
            }
        }
        .active{
            background-color: @skin-brand-primary;
            border:solid 1px  @skin-brand-primary;
        }
    }
    .checked-custom{
        input[type="checkbox"] + span::before,
        input[type="radio"] + span::before{
            color: @skin-brand-primary;
        }
    }
    .custom-h::before{
        background: #ffa82a;
		color:#FFA82A;
    }
    .btn{
        border-color: rgba(0,0,0,.1);
        &.btn-default{
            background: #f9f9f9;
            &.active,
            &:active:focus{
                background: darken(#f9f9f9, 6.5%);
                border-color: darken(rgba(0,0,0,.1),15%);
            }
        }
        &.btn-primary{
            background: @skin-brand-primary;
            &.active,
            &:active:focus{
                background: darken(@skin-brand-primary, 6.5%);
                border-color: darken(@skin-brand-primary,15%);
            }
        }
    }
}